const express = require('express');
const router = express.Router();
const verifyToken = require('../middleware/auth'); // Token doğrulama middleware'i
const Termin = require('../models/termin'); // Termin modelini import et

// Termin oluşturma işlemi
router.post('/termin', verifyToken, async (req, res) => {
    try {
        const { email, datum, time } = req.body;
        // Burada token doğrulandıktan sonra, termin kaydını oluştur
        const termin = new Termin({
            email,
            datum,
            time,
        });
        await termin.save();
        res.status(201).json({ message: 'Termin erfolgreich erstellt' });
    } catch (error) {
        console.error(error);
        res.status(500).json({ message: 'Termin oluşturulurken bir hata oluştu' });
    }
});

module.exports = router;
